	module.exports = {"data":{"markdownRemark":{"html":"<p>The Scottish government has an excellent collection of resources regarding the use of Information Communication Technology in classrooms. They have multi-part articles grouped by subject (Biology, Drama, Physics), and examples of how, for example, you can show students living cells using microscope and digital camera, or how to use the internet to teach French. Most of it is not Scotland-specific, though they do feature Scottish schools. </p>\n<p>My major criticism of the site is that it doesn't explore low-cost options (such as using open source software). Correspondingly, this site won't be as useful in less-developed countries.  That said, it is a great example of free, user-friendly tutorials for \"best practices\" education.\nHere's the site:\n<a title=\"Home - Secondary\" href=\"http://www.ltscotland.org.uk/ictineducation/effectivepractice/secondary/index.asp#0\">www.ltscotland.org.uk - Secondary</a></p>","frontmatter":{"created_at":"September 25, 2005","path":"/2005/09/25/ict-in-education","title":"ICT in Education","tags":["education","language","opensource","gov","oer"]}}},"pathContext":{}}

